<template>
  <div class="home">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="requests-layout">
      <div class="public-rooms">
        <div class="div-hdr">
          <h5><b>طلبات العضويات</b></h5>
        </div>
        <div class="text-center text-danger my-2" v-show="isLoading">
          <b-spinner class="align-middle"></b-spinner>
        </div>
        <div class="requests-body" v-show="!isLoading">
          <b-row>
            <b-col lg="10" md="12" sm="12" v-if="requests.length == 0">
              <div class="pt-5 no-content" style="text-align:center">
                <img src="../assets/images/data-not-found.png">
                <h4> لا يوجد طلبات</h4>
              </div>
            </b-col>
            <b-col lg="10" md="12" sm="12" v-for="(request, index) in requests" :key="index">
              <div class="request-col">
                <b-row align-v="center">
                  <b-col lg="7" md="7" sm="12">
                    <b-media vertical-align="center">
                      <template v-slot:aside>
                        <b-avatar class="image" :src="request.student.image"></b-avatar>
                      </template>
                      <h5 class="mt-3">{{request.student.full_name}} يريد ان ينضم الي غرفة {{request.room.name}}</h5>
                    </b-media>
                  </b-col>
                  <b-col lg="5" md="5" sm="12">
                    <div class="action-buttons">
                      <b-button variant="primary" @click="actionRequest(request.id, 'accept')">قبول الطلب</b-button>
                      <b-button class="mr-2" variant="light" @click="actionRequest(request.id, 'blocked')"><i class="far fa-trash-alt delete-icon"></i></b-button>
                    </div>
                  </b-col>
                </b-row>
              </div>
            </b-col>
          </b-row>
        </div>
      </div>
    </div>
 </div>
</template>

<script>
  import store from "../store/index.js"
  import { sendRequest }  from "../utils/RequestHelper.js"
  
  export default {
    name: 'Home',
    data() {
      return {
        isLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        requests: []
      }
    },
    methods: {
      actionRequest(id, action) {
        this.isLoading = true;
        var message = null;
        if(action == 'accept')
          message = "تم قبول الطالب بنجاح";
        else
          message = "تم رفض الطالب بنجاح";
        var successCallback = () => {
          this.alertType = "success"
          this.alertText = message
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
          this.loadRequests();
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('put', '/teacher/change-join-request-status/' + id + '/' + action, true, {} , successCallback, errorCallback);
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      loadRequests() {
        this.isLoading = true;
        var successCallback = (response) => {
          this.requests = response.data.data;
          this.isLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/teacher/show-join-requests', true, null , successCallback, errorCallback);
      }
    },
    created() {
      if(store.getters.authToken == null) {
        store.commit("SET_TOKEN", null);
        store.commit("SET_USERNAME", null);
        this.$router.push({
          name: 'login'
        })
      }
      this.loadRequests();
    }
  }
</script>
