               <b-form-timepicker  
                  id="admin-add-time"
                  name="admin-add-time"
                  v-model="$v.editForm.time.$model"
                  :state="validateState('time')"
                  aria-describedby="input-add-time"
                ></b-form-timepicker  >
                <b-form-invalid-feedback id="input-add-time">
                  موعد الدرس مطلوب
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
            <b-col lg="12" md="12">
